import React from 'react';
import Timer from 'react-compound-timer'

const TimerSection = ({
    lotterySeconds,
    ...props
}) => {

    let sec = lotterySeconds * 1000;
    return (
        <div style={{fontSize: '0.6em'}}>
            {lotterySeconds != 0 && <Timer
                initialTime={sec}
                direction="backward"
                checkpoints={[
                    {
                        time: 60000 * 60 * 48 - 5000,
                        callback: () => console.log('Checkpoint B'),
                    }
                ]}
            >
                {/* <Timer.Days /> <span style={{display: 'inline-block', marginRight: '10px'}}>:</span> */}
                <Timer.Hours /> <span style={{display: 'inline-block', marginRight: '10px'}}>:</span>
                <Timer.Minutes /> <span style={{display: 'inline-block', marginRight: '10px'}}>:</span>
                <Timer.Seconds /> <span style={{display: 'inline-block'}}></span>
            </Timer>}
        </div>
    );
}

export default TimerSection;