      tier2Pot = Number(web3.utils.fromWei(tier2Pot));
      setTier2PotSize(Math.trunc(Number(tier2Pot)));
      setTier1PotSize(Math.trunc(Number(tier1Pot)));
      setTier0PotSize(Math.trunc(Number(tier0Pot)));
      setTier0Holders(tier0Holder);
      setTier1Holders(tier1Holder);
      setTier2Holders(tier2Holder);
